import { Component, OnInit } from '@angular/core';
import { LoadingController } from '@ionic/angular';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, NgForm, Validators, FormArray } from '@angular/forms';
import { ToastController } from '@ionic/angular';

import { PersonService } from '../shared/person.service';
import { ProfileService } from '../shared/profile.service';

@Component({
  selector: 'app-create',
  templateUrl: './create.page.html',
  styleUrls: ['./create.page.scss'],
})
export class CreatePage implements OnInit {
  personForm: FormGroup;
  profiles: any;

  constructor(
    public personService: PersonService,
    public profileService: ProfileService,
    public loadingController: LoadingController,
    private route: ActivatedRoute,
    public router: Router,
    private formBuilder: FormBuilder,
    public toastController: ToastController
  ) {
    this.personForm = this.formBuilder.group({
      'name': [null, Validators.required],
      'cpf': [null, Validators.required],
      'rg': [null, Validators.required],
      'birthday': [null, Validators.required],
      'profile_id': [null, Validators.required]
    });
  }

  ngOnInit() {
    this.getProfiles();
  }

  async getProfiles() {
    const loading = await this.loadingController.create({
      message: 'Loading'
    });
    await loading.present();
    await this.profileService.getAll()
      .subscribe(res => {
        console.log(res);
        this.profiles = res;
        loading.dismiss();
      }, err => {
        console.log(err);
        loading.dismiss();
        this.presentToast('Erro ao consultar. Tente novamente!');
      });
  }

  async presentToast(message: string) {
    const toast = await this.toastController.create({
      message: message,
      duration: 2000,
      showCloseButton: true,
      closeButtonText: 'Fechar'
    });
    toast.present();
  }

  async save() {
    console.log(this.personForm.value);
    await this.personService.post(this.personForm.value)
      .subscribe(res => {
        const id = res['id'];
        this.router.navigate(['/detail-person/' + id]);
      }, (err) => {
        console.log(err);
        this.presentToast('Erro ao salvar. Tente novamente!');
      }, () => {
        this.presentToast('Pessoa cadastrada com sucesso!');
      });
  }
}
